export function hasField(
  object: any,
  fieldNamePredicate: (s: string) => boolean
): boolean {
  if (!object) {
    return false
  }
  if (Array.isArray(object)) {
    return object.some((ele) => hasField(ele, fieldNamePredicate))
  }
  if (typeof object === 'object') {
    const keys = Object.keys(object)
    return keys.some(
      (k) => fieldNamePredicate(k) || hasField(object[k], fieldNamePredicate)
    )
  }
  return false
}
